import React from "react";
import SearchByRest from "./searchByRest";
import SearchByLoc from "./searchByLoc";
import { Link } from "react-router-dom";

class SearchBar extends React.Component {
  constructor(props) {
    super(props);
    this.state = {
      city: "",
      category: "",
      searchValue: "",
      searchBy: "restaurant",
    };
    this.handleSearchValueChange = this.handleSearchValueChange.bind(this);
    this.handleCategoryChange = this.handleCategoryChange.bind(this);
    this.handleCityChange = this.handleCityChange.bind(this);
    // this.handleLocationChange = this.handleLocationChange.bind(this);
    this.handleSearch = this.handleSearch.bind(this);
    this.searchOptions = {
      "Search By Restaurant": "restaurant",
      "Search By Location": "location",
    };
  }

  getSearchClass(searchOption) {
    if (this.state.searchBy === searchOption) {
      return "active";
    } else {
      return "";
    }
  }

  handleSearchChange(searchOption) {
    this.setState({ searchBy: searchOption });
  }

  handleCityChange(e) {
    this.setState({ city: e.target.value });
  }

  handleCategoryChange(e) {
    this.setState({ category: e.target.value });
  }

  handleSearchValueChange(e) {
    this.setState({ searchValue: e.target.value });
  }

  getZone(area) {
    const zones = {
      dhanmondi: 1,
      panthapath: 1,
      gulshan: 2,
      baridhara: 2,
      "indira road": 1,
    };
    return zones[area];
  }
  handleSearch() {
    if (this.state.searchBy === "location") {
      const { searchValue, city, category } = this.state;
      let zone = this.getZone(searchValue.toLowerCase());
      // console.log(
      //   `?zone=${zone}&location__city__city_name__icontains=${city}&restaurant__category__name__icontains=${category}`
      // );
      const endpoints = `?place=${searchValue}&city=${city}&category=${category}`;
      return `/locations${endpoints}`;
    } else {
      const { searchValue } = this.state;
      const endpoints = `?name__icontains=${searchValue}`;
      return `/restaurants${endpoints}`;
    }
  }

  renderSearchOptions() {
    return Object.keys(this.searchOptions).map((searchOption) => {
      let searchOptionValue = this.searchOptions[searchOption];
      return (
        <li
          key={searchOptionValue}
          className={this.getSearchClass(searchOptionValue)}
          onClick={this.handleSearchChange.bind(this, searchOptionValue)}
        >
          {searchOption}
        </li>
      );
    });
  }
  render() {
    return (
      <div className="SearchBar">
        <div className="SearchBar-sort-options">
          <ul>{this.renderSearchOptions()}</ul>
        </div>
        {this.state.searchBy === "restaurant" && (
          <SearchByRest
            value={this.state.searchValue}
            onChange={this.handleSearchValueChange}
          />
        )}
        {this.state.searchBy === "location" && (
          <SearchByLoc
            value={this.state.searchValue}
            city={this.state.city}
            onChange={this.handleSearchValueChange}
            onCityChange={this.handleCityChange}
            category={this.state.category}
            onCategoryChange={this.handleCategoryChange}
          />
        )}
        <div className="SearchBar-submit">
          <Link to={this.handleSearch}>Let's Go</Link>
        </div>
      </div>
    );
  }
}

export default SearchBar;
