import React from "react";
import { makeStyles, useTheme } from "@material-ui/core/styles";
import Card from "@material-ui/core/Card";

import Typography from "@material-ui/core/Typography";

import Avatar from "@material-ui/core/Avatar";
import Grid from "@material-ui/core/Grid";
import Rating from "@material-ui/lab/Rating";
const reviews = [
  {
    id: 1,
    title: "Best Kacchi in Dhaka",
    description: "Best, awesome, superb...just too good.",
    rating: 5,
    timestamp: "2020-07-14T15:16:24",
    restaurant: 1,
    user: "sec c Tanvir",
  },
  {
    id: 2,
    title: "Sultans Kacchi  best Kacchi",
    description: "Just amazing. Must try.",
    rating: 5,
    timestamp: "2020-07-14T15:16:24",
    restaurant: 1,
    user: "Sadman Siam",
  },
];
const useStyles = makeStyles((theme) => ({
  root: {
    display: "flex",
    paddingBottom: theme.spacing(4),
  },
  details: {
    display: "flex",
    flexDirection: "column",
  },
  content: {
    flex: "1 0 auto",
  },
  cover: {
    width: 151,
  },
  controls: {
    display: "flex",
    alignItems: "center",
    paddingLeft: theme.spacing(1),
    paddingBottom: theme.spacing(1),
  },
  large: {
    width: theme.spacing(7),
    height: theme.spacing(7),
  },
  playIcon: {
    height: 38,
    width: 38,
  },
  heda: {
    flexGrow: 1,
    overflow: "hidden",
    padding: theme.spacing(0, 3),
  },
}));

export default function Review() {
  const classes = useStyles();
  const theme = useTheme();

  return reviews.map((review) => (
    <Card className={classes.root}>
      <div className={classes.heda}>
        <Grid container spacing={2}>
          <Grid item>
            <Avatar
              alt="Remy Sharp"
              src="data:image/jpeg;base64,"
              className={classes.large}
            />
          </Grid>
          <Grid item>
            <Typography>{review.user}</Typography>
            <Typography variant="subtitle1" color="textSecondary">
              panthapath,Dhaka
            </Typography>
            <Rating
              name="half-rating read-only"
              value={3.5}
              readOnly
              precision={0.5}
            />
          </Grid>
          <Grid item xs>
            <Typography variant="" color="textSecondary">
              {review.title}
            </Typography>
            <Typography variant="subtitle1" color="textSecondary">
              {review.timestamp}
            </Typography>
            <Typography>{review.description}</Typography>
          </Grid>
        </Grid>
      </div>
    </Card>
  ));
}
