import React, { Component } from "react";
import { getRestaurants } from "./../../services/restaurants";
import "./css/results_styles.css";
import CardComp from "./cardComp";
import Grid from "@material-ui/core/Grid";
const queryString = require("query-string");

class RestaurantSearch extends Component {
  state = {
    query: "",
    data: [],
  };

  async componentDidMount() {
    const { data } = await getRestaurants(this.state.query);
    // console.log(`query: ${this.state.query}`);
    // console.log(`query: ${data}`);
    this.setState({ data });
  }

  async componentDidUpdate() {
    if (this.state.query !== this.props.location.search) {
      const { data } = await getRestaurants(this.props.location.search);
      this.setState({ query: this.props.location.search, data });
    }
  }

  getZone() {}

  render() {
    let view = "All Restaurants";
    const result = queryString.parse(this.props.location.search);
    if (Object.keys(result).length !== 0) {
      view = `You searched for ${result.name__icontains}`;
    }
    return (
      <React.Fragment>
        <Grid item xs={12}>
          <h5>{view}</h5>
        </Grid>
        <Grid item xs={12}>
          <CardComp data={this.state.data} />
        </Grid>
      </React.Fragment>
    );
  }
}

export default RestaurantSearch;
