import React from "react";
import { makeStyles } from "@material-ui/core/styles";
import Card from "@material-ui/core/Card";
import CardContent from "@material-ui/core/CardContent";
import CardMedia from "@material-ui/core/CardMedia";
import Typography from "@material-ui/core/Typography";
import Rating from "@material-ui/lab/Rating";

import Button from "@material-ui/core/Button";

const useStyles = makeStyles((theme) => ({
  root: {
    display: "flex",
    // minHeight: "200px"
  },
  details: {
    display: "flex",
    flexDirection: "column",
  },
  content: {
    flex: "1 0 auto",
  },
  cover: {
    width: "30%",
    minWidth: "150px",
  },
  button: {
    marginTop: 40,
  },
  results: {
    marginTop: 15,
    marginBottom: 15,
  },
  img: {
    objectFit: "cover",
  },
}));

export default function BranchCard({ data }) {
  const classes = useStyles();

  return data.map((branch) => (
    <div className={classes.results}>
      <Card className={classes.root}>
        <CardMedia
          className={`${classes.cover} ${classes.img}`}
          image={branch.restaurant.image}
          title={"Live from space album cover"}
        />
        <div className={classes.details}>
          <CardContent className={classes.content}>
            <Typography component="h5" variant="h5">
              {`${branch.restaurant.name} ${branch["name"]}`}
            </Typography>
            <Typography variant="subtitle1" color="textSecondary"></Typography>
            <Typography variant="subtitle1" color="textSecondary">
              {branch.restaurant.category.name}
            </Typography>
            <Rating
              name="half-rating read-only"
              value={3.5}
              readOnly
              precision={0.5}
            />
            <div className={classes.button}>
              <Button variant="contained" color="primary">
                Order Now
              </Button>
            </div>
          </CardContent>
        </div>
      </Card>
    </div>
  ));
}
