import React, { Component } from "react";
// import "./css/demo.css";
import DetailsComp from "./detailsComp";

import { getBranch } from "./../../services/branch";
import { getFoods } from "./../../services/food";
import FoodMenu from "./foodMenu";
import Review from "./review/review";
import Grid from "@material-ui/core/Grid";

class RestaurantsDetails extends Component {
  state = {
    foods: [],
  };

  async componentDidMount() {
    const { data } = await getBranch(1);
    const { data: foods } = await getFoods(1);
    console.log(foods);
    this.setState({ data, foods });
  }
  render() {
    const data = this.state.data;
    const foods = this.state.foods;
    const loading = data == null || foods == null;
    console.log(loading);
    return (
      <Grid item container xs={12} spacing={3}>
        <Grid item container xs={12} className="box">
          {loading ? (
            <p>Loading</p>
          ) : (
            <DetailsComp restaurant={data.restaurant} branch={data} />
          )}
        </Grid>

        <Grid container item xs={12} spacing={0} className="box">
          {loading ? <p>Loading</p> : <FoodMenu foods={foods} />}
        </Grid>

        <Grid container item xs={12} spacing={0} className="box">
          <Grid item xs={12} sm={12} lg={6}>
            <Review />
          </Grid>
          <Grid item xs={12} sm={12} lg={6}>
            youtube
          </Grid>
        </Grid>
      </Grid>
    );
  }
}

export default RestaurantsDetails;
