import React, { Component } from "react";
import "./css/card.css";
import { withStyles } from "@material-ui/core/styles";
import Grid from "@material-ui/core/Grid";
import Paper from "@material-ui/core/Paper";
import Typography from "@material-ui/core/Typography";
import ButtonBase from "@material-ui/core/ButtonBase";

const useStyles = (theme) => ({
  root: {
    flexGrow: 1,
  },
  paper: {
    padding: theme.spacing(1),
    margin: "auto",
    maxWidth: 500,
    marginBottom: theme.spacing(7),
  },
  image: {
    width: 100,
    height: 100,
  },
  img: {
    margin: "auto",
    display: "block",
    maxWidth: "100%",
    maxHeight: "100%",
  },
});

class FoodMenu extends Component {
  foods = [
    {
      id: 1,
      image: null,
      name: "Kacchi - Basmati",
      category: "Polao",
      price: 399.0,
      description: "Kacchi platter enough for 2 person",
      restaurant: 1,
    },
    {
      id: 1,
      image: null,
      name: "Kacchi - Basmati",
      category: "Polao",
      price: 399.0,
      description: "Kacchi platter enough for 2 person",
      restaurant: 1,
    },

    {
      id: 1,
      image: null,
      name: "Kacchi - Basmati",
      category: "Polao",
      price: 399.0,
      description: "Kacchi platter enough for 2 person",
      restaurant: 1,
    },
    {
      id: 1,
      image: null,
      name: "Kacchi - Basmati",
      category: "Kacchi",
      price: 399.0,
      description: "Kacchi platter enough for 2 person",
      restaurant: 1,
    },
    {
      id: 1,
      image: null,
      name: "Kacchi - Basmati",
      category: "Kacchi",
      price: 399.0,
      description: "Kacchi platter enough for 2 person",
      restaurant: 1,
    },
    {
      id: 1,
      image: null,
      name: "Kacchi - Basmati",
      category: "Kacchi",
      price: 399.0,
      description: "Kacchi platter enough for 2 person",
      restaurant: 1,
    },
  ];
  getMenuByCat(data) {
    let cat = [];
    data.map((item) => {
      cat.push(item.category);
    });
    let mySet = new Set(cat);
    let res = [];
    let tmp = [];
    var obj = {};
    for (let item of mySet) {
      tmp = data.filter((food) => food.category === item);
      obj = {};
      obj[item] = tmp;
      res.push(obj);
    }
    // console.log(res);
    return res;
  }

  render_menu(data) {
    const { classes } = this.props;
    return (
      <Grid container>
        {data.map((items) => (
          <Paper className={classes.paper} md={6} sm={6} xs={12} lg={6} xl={6}>
            <Grid item justify="center" alignItems="center" container>
              <Grid item>
                <ButtonBase className={classes.image}>
                  <img
                    className={classes.img}
                    alt="complex"
                    src="/static/images/grid/complex.jpg"
                  />
                </ButtonBase>
              </Grid>

              <Grid item>
                <Typography gutterBottom variant="subtitle1">
                  {data.name}
                </Typography>
                <Typography variant="body2" gutterBottom>
                  Full resolution 1920x1080 â€¢ JPEG
                </Typography>
                <Typography variant="body2" color="textSecondary">
                  ID: 1030114
                </Typography>
              </Grid>
            </Grid>
          </Paper>
        ))}
      </Grid>
    );
  }
  render() {
    const categories = this.getMenuByCat(this.foods);
    // console.log(categories);
    // console.log(this.props.foods);
    const { classes } = this.props;
    return (
      <div className={classes.root}>
        <React.Fragment>
          {categories.map((cat) => (
            <Grid item container xs={12}>
              <h1>{Object.keys(cat)}</h1>
              {this.render_menu(cat[Object.keys(cat)])}
            </Grid>
          ))}
        </React.Fragment>
      </div>
    );
  }
}
export default withStyles(useStyles)(FoodMenu);
