import React, { Component } from "react";
import queryString from "query-string";
import { getBranches } from "./../../services/branch";
import BranchCard from "./branchCard";
import { Grid } from "@material-ui/core";

class RestaurantSearch extends Component {
  state = {
    query: "",
    data: [],
  };

  async componentDidMount() {
    const { data } = await getBranches(this.state.query);
    // console.log(`query: ${this.state.query}`);
    // console.log(`query: ${data}`);
    this.setState({ data });
  }

  async componentDidUpdate() {
    if (this.state.query !== this.props.location.search) {
      const queryObj = queryString.parse(this.props.location.search);
      // query is different from api
      if (Object.keys(queryObj).length === 0) {
        console.log("nothing");
      } else {
        let { category, place, city } = queryObj;
        let zone = 1;
        const endpoints = `?zone=${zone}&location__city__city_name__icontains=${city}&restaurant__category__name__icontains=${category}`;

        const { data } = await getBranches(endpoints);
        this.setState({ data, query: this.props.location.search });
      }

      //   const { data } = await getRestaurants(this.props.location.search);
      //   console.log(data);
      this.setState({ query: this.props.location.search });
    }
  }

  getZone() {}

  render() {
    return (
      <React.Fragment>
        <Grid item xs={12}>
          <h3>All restaurants near you</h3>
        </Grid>
        <Grid item xs={12}>
          <BranchCard data={this.state.data} />
        </Grid>
      </React.Fragment>
    );
  }
}

export default RestaurantSearch;
