import React, { Component } from "react";
import { Grid, Paper } from "@material-ui/core";

class DetailsComp extends Component {
  constructor(props) {
    super(props);
    this.getRatingStar = this.getRatingStar.bind(this);
  }

  getRatingStar(rating) {
    let total = 5.0;
    let rem = total - rating;
    let count = rating;

    let res = [];
    let i = 1;
    for (; i <= count; ) {
      res.push(<i key={i} className="fa fa-star" aria-hidden="true"></i>);
      i++;
    }
    i = i - 1;
    if (i < count) {
      res.push(<i key={i} className="fa fa-star-half-full"></i>);
      i += 1;
    }

    rem = total - i;
    for (let j = 1; i <= rem; j++) {
      res.push(<i key={j + i} className="fa fa-star-o" aria-hidden="true"></i>);
    }

    return res;
  }

  render() {
    const branch = this.props.branch;
    const restaurant = this.props.restaurant;
    let res = this.getRatingStar(restaurant.ratings);
    return (
      <Grid item container xs={12} spacing={3} className="box">
        <Grid item xs={12} lg={8} sm={6} xl={8}>
          <h2>{restaurant.name}</h2>
          <p>
            <strong>Descriptiion:</strong> {restaurant.description}
          </p>
          <p>
            <strong>Ratings: </strong>
            {res.map((item) => item)}
          </p>
          <p>
            <strong>Address: </strong>
            {`${branch.address}`}
          </p>
        </Grid>
        {/* <Grid xs={0} lg{2/> */}
        <Grid item xs={12} lg={4} sm={6} xl={4}>
          <Paper
            style={{
              backgroundImage: `url(${restaurant.image})`,
              minHeight: 200,
              height: "100%",
            }}
          >
            sdfafsfd
          </Paper>
        </Grid>
      </Grid>
    );
  }
}
export default DetailsComp;
