import React from "react";
import { makeStyles } from "@material-ui/core/styles";
import Card from "@material-ui/core/Card";
import CardContent from "@material-ui/core/CardContent";
import CardMedia from "@material-ui/core/CardMedia";
import Typography from "@material-ui/core/Typography";
import Rating from "@material-ui/lab/Rating";

import Button from "@material-ui/core/Button";
import RestaurantsDetails from "./../restaurant/restaurantsDetails";
import { Link } from "react-router-dom";

const useStyles = makeStyles((theme) => ({
  root: {
    display: "flex",
    // minHeight: "200px"
  },
  details: {
    display: "flex",
    flexDirection: "column",
  },
  content: {
    flex: "1 0 auto",
  },
  cover: {
    width: "30%",
    minWidth: "150px",
  },
  button: {
    marginTop: 40,
  },
  results: {
    marginTop: 15,
    marginBottom: 15,
  },
}));

export default function CardComp({ data }) {
  const classes = useStyles();

  return data.map((restaurant) => (
    <div className={classes.results}>
      <Card className={classes.root}>
        <CardMedia
          className={classes.cover}
          image={restaurant.category.thumbnail}
          title={"Live from space album cover"}
        />
        <div className={classes.details}>
          <CardContent className={classes.content}>
            <Typography component="h5" variant="h5">
              {restaurant["name"]}
            </Typography>
            <Typography variant="subtitle1" color="textSecondary"></Typography>
            <Typography variant="subtitle1" color="textSecondary">
              {restaurant.category.name}
            </Typography>
            <Rating
              name="half-rating read-only"
              value={3.5}
              readOnly
              precision={0.5}
            />
            <div className={classes.button}>
              <Link to={`/restaurants/details/${restaurant.id}`}>
                <Button variant="contained" color="primary">
                  Order Now
                </Button>
              </Link>
            </div>
          </CardContent>
        </div>
      </Card>
    </div>
  ));
}
