import React from "react";
import { Switch, Route, Redirect } from "react-router-dom";
import "bootstrap/dist/css/bootstrap.min.css";
import HomePage from "./home/homePage";
import RestaurantSearch from "./results/restaurantsSearch";
import LocationSearch from "./results/locationSearch";
import RestaurantsDetails from "./restaurant/restaurantsDetails";
import SearchBar from "./searchBar/searchBar";
import NavBar from "./common/navbar";
import Grid from "@material-ui/core/Grid";
import LoginForm from "./user/loginForm";
import ReviewAdd from "./restaurant/review/reveiwAdd";
import SignUp from "./user/registrationForm";

function Base({ user }) {
  return (
    <React.Fragment>
      <Grid item xs={12} className="box">
        <NavBar user={user} />
        <SearchBar />
      </Grid>

      <Grid role="main" container item xs={12}>
        <Switch>
          <Route
            path="/restaurants/details/:id/reviews/add"
            exact
            component={ReviewAdd}
          />
          <Route path="/user/login" exact component={LoginForm} />
          <Route path="/user/registration" exact component={SignUp} />
          <Route
            path="/restaurants/details/:id"
            exact
            component={RestaurantsDetails}
          />
          <Route path="/restaurants" exact component={RestaurantSearch} />
          <Route path="/locations" exact component={LocationSearch} />

          <Route path="/" exact component={HomePage} />
        </Switch>
      </Grid>
      <Grid container xs={0} xl={2} lg={2} />
    </React.Fragment>
  );
}

export default Base;
