import React from "react";

import { Navbar, Nav, Button } from "react-bootstrap";

const NavBar = ({ user }) => {
  return (
    <Navbar className="col-12 navBar" bg="white" expand="lg">
      <Navbar.Brand href="#home">Kothay Ki</Navbar.Brand>
      <Navbar.Toggle aria-controls="basic-navbar-nav" />
      <Navbar.Collapse id="basic-navbar-nav">
        <Nav className="mr-auto">
          <Nav.Link href="#home">Home</Nav.Link>
          <Nav.Link href="#features">Features</Nav.Link>
          <Nav.Link className="outline-info" href="#pricing">
            Pricing
          </Nav.Link>
        </Nav>
        <Nav className="mr justify-content-end">
          {/* {user && <Nav.Link href="#home">{user.email}</Nav.Link>} */}
          {!user && (
            <Button className="bl" variant="outline-info">
              <i className="fa fa-sign-in" aria-hidden="true"></i>
              Sign-Up
            </Button>
          )}
        </Nav>
      </Navbar.Collapse>
    </Navbar>
  );
};

export default NavBar;
