import restService from "./restService";
import config from "../config.json";

export function getFoods(restId) {
  return restService.get(
    `${config.apiBaseURL}/api/v1/restaurants/${restId}/foods`
  );
}

export function getFood(id) {
  return restService.get(`${config.apiBaseURL}/api/v1/restaurants/foods/${id}`);
}
