import restService from "./restService";
import config from "../config.json";

export function getBranches(param = "") {
  return restService.get(
    `${config.apiBaseURL}/api/v1/restaurants/branches${param}`
  );
}

export function getBranch(id) {
  return restService.get(
    `${config.apiBaseURL}/api/v1/restaurants/branches/${id}`
  );
}
